*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body{
  /* background-color: aqua; */
}

p{
  font-size: 1.6rem;
}

h1{
  font-size: 3rem;
}
.app{
 width:100%;
 height: 100vh;
 position: relative;
 background-color: rgba(0,0,0,0.4);
 color: white;
}

.app::before{
  content:'';
  background: url('./Images/sunset.jpg') no-repeat center center/cover;
  position:absolute;
  width:  100%;
  height:100vh;
  top:0;
  left:0;
  z-index: -1;
}

.app .search{
  text-align: center;
  padding: 1 rem;
  
}

.app input{
  padding: .7rem 1.5rem;
  font-size: 1rem;
  margin-top: 1.5rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255,0.8);
  background: rgba(0,0,0,0.1);
  color: #f8f8f8;
}

input::placeholder{
  color: #f8f8f8;
}
.container{
  max-width: 700px;
  height:500px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top:10%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.app .top{
  width: 100%;
  margin: 1rem auto;
}

.app .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(268deg);
}

.app .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.3);
}

.bold{
  font-weight: 500;
}
